import Vue from 'vue'
import Router from 'vue-router'
Vue.use(Router)

export default new Router({
  // mode: 'history',
  routes: [
    {
      path: '/ds',
      name: 'dasai',
      component: () => import('@/page/index/index.vue'),
      children: [
        {
          path: 'apply',
          meta: {
            title: '个人赛况，身份认证'
          },
          component: () => import('@/page/apply/apply.vue')
        },
        {
          path: 'auth',
          meta: {
            title: '个人赛况-我的大会，大会报名'
          },
          component: () => import('@/page/apply/auth.vue')
        },
        {
          path: 'competition',
          meta: {
            title: '个人赛况-我的大会，我要参赛'
          },
          component: () => import('@/page/competition/competition.vue')
        },
        {
          path: 'detail',
          component: () => import('@/page/works/detail.vue')
        }
      ]
    },
    {
      path: '/option',
      name: 'option',
      component: () => import('@/page/lsIndex/lsIndex.vue'),
      children: [
        {
          path: 'once',
          name: 'once',
          meta: {
            title: '选择场次',
            auth: true
          },
          component: () => import('@/components/optionComp/option.vue')
        },
        {
          path: 'nameList',
          name: 'nameList',
          meta: {
            title: '晋级名单',
            auth: true
          },
          component: () => import('@/page/lsDome/nameList/nameList.vue')
        },
        {
          path: 'allWorks',
          name: 'allWorks',
          meta: {
            title: '全部作品',
            auth: true
          },
          component: () => import('@/page/lsDome/allWorks/allWorks.vue')
        },
        {
          path: 'gameWorks',
          name: 'gameWorks',
          meta: {
            title: '大赛作品',
            auth: true
          },
          component: () => import('@/page/lsDome/gameWorks/gameWorks.vue')
        },
        {
          path: 'rule',
          name: 'rule',
          meta: {
            title: '大赛规则',
            auth: true
          },
          component: () => import('@/page/lsDome/rule/rule.vue')
        },
        {
          path: 'gameBj',
          name: 'gameBj',
          meta: {
            title: '大赛背景',
            auth: true
          },
          component: () => import('@/page/lsDome/gameBj/gameBj.vue')
        },
        {
          path: 'juryRe',
          name: 'juryRe',
          meta: {
            title: '评委简介',
            auth: true
          },
          component: () =>
            import('@/page/lsDome/juryRecommend/juryRecommend.vue')
        },
        {
          path: 'gameIntroduce',
          name: 'gameIntroduce',
          meta: {
            title: '大赛介绍',
            auth: true
          },
          component: () =>
            import('@/page/lsDome/gameIntroduce/gameIntroduce.vue')
        },
        {
          path: 'list',
          name: 'list',
          meta: {
            title: '列表',
            auth: false
          },
          component: () => import('@/page/lsDome/list/list.vue')
        }
      ]
    }
  ]
})



