import request from '../axios'
// 大会介绍-大会介绍详情
const describe = (data) => {
  return request({
    url: 'introduction',
    method: 'get',
    data
  })
}
// 大会介绍-获取评委列表
/**
 * @data => {per_page, page}
 * per_page 每页显示条数 默认10条每页
 * page 查询页码
 */
const juryList = data => {
  return request({
    url: 'judge',
    method: 'get',
    data
  })
}
// 大会介绍-获取评委详情
/**
 * @param {*} data judge => number
 */
const juryDescribe = (judge, data) => {
  return request({
    url: `judge/${judge}`,
    method: 'get',
    data
  })
}
// 大会介绍-获取协作单位列表
/**
 * @param {*} data
 * per_page => 每页显示条数 默认10条每页
 * page 显示页码
 */
const unitList = data => {
  return request({
    url: 'unit',
    method: 'get',
    data
  })
}
// 大会介绍-获取协作单位详情
/**
 * @param {*} data
 * unit = number
 */
const unitDescribe = (unit, data) => {
  return request({
    url: `unit/${unit}`,
    method: 'get',
    data
  })
}
export default { describe, juryDescribe, juryList, unitList, unitDescribe }



