import model from '../../../api/index'
// 大会介绍信息
export default {
  namespaced: true,
  state: {
    // 用户信息
    info: {},
    // 视频信息
    videoInfo: {
      name: '',
      cover_url: '',
      file_id: '',
      media_url: '',
      desc: null
    },
    // 协作单位
    units: [{
      'id': 0,
      'name': '',
      'type': '',
      'image_url': '',
      'url': ''
    }],
    // 大会评委
    judges: [{
      'id': 1,
      'name': '', // 姓名
      'avatar': '', // 头像地址
      'mobile': '', // 手机号
      'desc': '' // 简介
    }]
  },
  // 异步修改
  actions: {
    // 获取视频info
    getVideoInfo ({ commit }) {
      return new Promise(async resolve => {
        const { describe } = model.gameIntroduction
        const action = await describe()
        commit('updateVideo', action.video)
        commit('updateUnits', action.units)
        commit('updateJudges', action.judges)
        resolve(action)
      })
    },
    getUnitList ({ commit }) {
      return new Promise(async resolve => {
        const { juryList } = model.gameIntroduction
        const action = await juryList()
        commit('updateVideo', action.video)
        resolve(action)
      })
    }
  },
  // 同步修改
  mutations: {
    updateVideo (state, action) {
      state.videoInfo = action
    },
    updateUnits (state, action) {
      state.units = action
    },
    updateJudges (state, action) {
      state.judges = action
    }

  }
}



