import model from '../../../api/index'
// 大会介绍信息
export default {
  namespaced: true,
  state: {
    juryInfo: {
      avatar: '',
      name: '',
      phone: '',
      summary: ''
    }
  },
  // 异步修改
  actions: {
    // 获取视频info
    getJuryInfo ({ commit }, id) {
      return new Promise(async resolve => {
        const { juryDescribe } = model.gameIntroduction
        const action = await juryDescribe(id)
        commit('updateJuryInfo', action)
        resolve(action)
      })
    }
  },
  // 同步修改
  mutations: {
    updateJuryInfo (state, action) {
      state.juryInfo = action
    }
  }
}



